<template>
    <div id="editor"></div>
    <div id="field"></div>
</template>

<script>
import * as monaco from 'monaco-editor'
import * as JXG from 'jsxgraph'

monaco.languages.register({
    id: 'custom'
})

monaco.languages.setMonarchTokensProvider('custom', {
    keywords: [
        'IFBLOCK', 'ENDIF',
        'REPEAT', 'ENDREPEAT',
        'PROCEDURE', 'ENDPROC',
        'SET', 'CALL',
        'RIGHT', 'LEFT', 'UP', 'DOWN'
    ],
    tokenizer: {
        root: [
            [/[A-Z_$][\w$]*/, {
                cases: {
                    '@keywords': 'keyword',
                    '@default': 'identifier'
                }
            }],
            [/\d+/, 'number']
        ]
    }
})

function lex (code) {
    return code.split(/[ \n\t]+/).filter(function (item) {
        return [
            'RIGHT', 'UP', 'LEFT', 'DOWN',
            'SET', 'CALL',
            'PROCEDURE', 'ENDPROC',
            'REPEAT', 'ENDREPEAT',
            'IFBLOCK', 'ENDIF'
        ].includes(item) ||
        /[a-zA-Z][a-zA-Z0-9]*/.test(item) ||
        /[0-9]+/.test(item)
    })
}

function parse (tokens, scope = {}, guard = true) {
    let cmds = ''

    const token = function () {
        return tokens.splice(0, 1)[0]
    }

    while (tokens.length) {
        const temp = token()
        if (temp === 'IFBLOCK') {
            cmds += `
                if (scope.blocked('${token()}') {
                    ${parse(tokens, scope, false)}
                }
            `
        } else if (temp === 'REPEAT') {
            cmds += `
                for (let pos = 0; pos < ${token()}; pos++) {
                    ${parse(tokens, scope, false)}
                }
            `
        } else if (temp === 'PROCEDURE') {
            // eslint-disable-next-line no-eval
            scope[token()] = eval(`(
                function(scope) {
                    ${parse(tokens, scope, false)}
                }
            )`)
        } else if (temp === 'CALL') {
            cmds += `
                scope.${token()}(scope)
            `
        } else if (temp === 'SET') {
            cmds += `
                scope.${token()} = ${token()}
            `
        } else if (['RIGHT', 'UP', 'LEFT', 'DOWN'].includes(temp)) {
            let arg = token()
            arg = isNaN(parseInt(arg)) ? `scope.${arg}` : arg
            cmds += `
                scope.${temp}(${arg})
            `
        } else if (['ENDIF', 'ENDREPEAT', 'ENDPROC'].includes(temp)) {
            return cmds
        }
    }

    console.log(cmds)
    return guard
        ? {
            // eslint-disable-next-line no-eval
            func: eval(
                `(
                function(scope) {
                    ${cmds}
                }
            )`),
            scope
        }
        : cmds
}

const configs = {
    strokeWidth: () => board.canvasWidth / 150,
    strokeColor: 'lightblue',
    highlightStrokeColor: 'lightblue'
}

function execute ({ func, scope }) {
    let x = 0.5; let y = 0.5
    func({
        ...scope,
        RIGHT: function (d) {
            board.create('segment', [[x, y], [x + d, y]], configs)
            x = x + d
        },
        UP: function (d) {
            board.create('segment', [[x, y], [x, y - d]], configs)
            y = y - d
        },
        LEFT: function (d) {
            board.create('segment', [[x, y], [x - d, y]], configs)
            x = x - d
        },
        DOWN: function (d) {
            board.create('segment', [[x, y], [x, y + d]], configs)
            y = y + d
        }
    })
}

let board

export default {
    name: 'app',
    async mounted () {
        const elem = document.querySelector('#editor')
        const editor = monaco.editor.create(elem, {
            value: '\n\n',
            language: 'custom',
            theme: 'vs-dark',
            // automaticLayout: true,

            scrollbar: {
                useShadows: false,

                verticalHasArrows: true,
                horizontalHasArrows: true,

                vertical: 'visible',
                horizontal: 'visible',

                verticalScrollbarSize: 17,
                horizontalScrollbarSize: 17,
                arrowSize: 30
            }
        })

        board = JXG.JSXGraph.initBoard('field', {
            boundingbox: [-0.5, -0.5, 22, 22],
            axis: true,
            defaultAxes: {
                x: {
                    strokeColor: 'white',
                    highlightStrokeColor: 'white',
                    ticks: {
                        insertTicks: false,
                        ticksDistance: 1,
                        minorHeight: 0,
                        strokeColor: 'white',
                        label: {
                            strokeColor: 'white',
                            highlightStrokeColor: 'white',
                            offset: [0, -4],
                            anchorX: 'middle',
                            anchorY: 'top'
                        }
                    }
                },
                y: {
                    strokeColor: 'white',
                    highlightStrokeColor: 'white',
                    ticks: {
                        insertTicks: false,
                        ticksDistance: 1,
                        minorHeight: 0,
                        strokeColor: 'white',
                        label: {
                            strokeColor: 'white',
                            highlightStrokeColor: 'white',
                            offset: [4, 0],
                            anchorX: 'left',
                            anchorY: 'middle'
                        }
                    }
                }
            },
            drag: {
                enabled: false
            },
            pan: {
                enabled: true,
                needShift: true
            }
        })

        document.querySelector('#field_licenseText').remove()
        addEventListener('resize', function (event) {
            editor.layout()
        })

        addEventListener('keydown', function (event) {
            if (event.keyCode !== 115) return
            execute(parse(lex(editor.getValue())))
        })

        const walls = {}
        board.on('down', function (event, i) {
            const cPos = board.getCoordsTopLeftCorner(event, i)
            const absPos = JXG.getPosition(event, i)
            const dx = absPos[0] - cPos[0]
            const dy = absPos[1] - cPos[1]

            let coords = new JXG.Coords(JXG.COORDS_BY_SCREEN, [dx, dy], board)
            const [_, x, y] = coords.usrCoords

            const [lx, mx, ux] = [Math.floor(x), Math.round(x), Math.ceil(x)]
            const [ly, my, uy] = [Math.floor(y), Math.round(y), Math.ceil(y)]

            if (Math.abs(mx - x) < 0.1) {
                coords = [[mx, ly], [mx, uy]]
            } else if (Math.abs(my - y) < 0.1) {
                coords = [[lx, my], [ux, my]]
            }

            if (coords instanceof JXG.Coords) {
                return
            }

            if (coords in walls) {
                board.removeObject(walls[coords])
                delete walls[coords]
            } else {
                walls[coords] = board.create('segment', coords, {
                    strokeColor: 'red',
                    highlightStrokeColor: 'red'
                })
            }
        })
    }
}
</script>

<style>
html, body {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
}

#app {
    height: 100%;
    width: 100%;

    display: flex;
}

#field {
    background-color: rgb(30, 30, 30);
}

@media (min-height: 100vw) {
    #app {
        flex-direction: column;
    }
    #editor, #field {
        height: 50vh;
        width: 100vw;
    }
}

@media (min-width: 100vh) {
    #app {
        flex-direction: row;
    }
    #editor, #field {
        height: 100vh;
        width: 50vw;
    }
}

</style>
